<template>
 <div class="webflow-style-input" :class="{'webflow-style-inputs':onFocusIs}">
    <input 
         class="basic-slide" 
         :id="id"  
         :class="[classValue]"
         :type="type"
         :value="modelValue"
         :placeholder="placeholder"
         @input="$emit('update:modelValue', $event.target.value)"
         @focus="onFocusEnable"
         @blur="onFocusDisable"
          :disabled="disabled"
         />

  </div>

</template>
<script>

export default {
    data(){
        return{
            onFocusIs : false
        }
    },
    methods:{
        onFocusEnable(){
            this.onFocusIs = true;
        },
        onFocusDisable(){
            this.onFocusIs = false;
        }
    },

    props: {
        label: {
            type: String,
            default:'',
        },

        disabled:{
            type:Boolean,
            default:false
        },

    
         id: {
            type: String,
            default:'',
        },
        borderdiv: {
            type: String,
            default:'',
        },
        modelValue: {
            type:[String,Number],
            default:'',
        },
        placeholder: {
            type: String,
            default:'',
        },
        type: {
            type: String,
            default:'text',
        },
        labelBorder: {
            type:[String,Number],
            default:'',
        },
        classValue: {
            type:[String,Number],
            default:'',
        },
    },
   
}
</script>

<style lang="scss" scoped>
.readonly{
    background-color:#ddd;
    pointer-events: none;
}
.back{
    background: #fff ;
    border: 1px solid #ddd !important;
}

.radious{
    border-radius: 5px !important;
}
    // colors
$input-background: rgba(57, 63, 84, 0.8);
$input-text-inactive: #7881A1;
$input-text-active: #BFD2FF;

// gradient animation
@keyframes gradient { 
  0%{background-position:0 0}
  100%{background-position:100% 0}
}

.webflow-style-input {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 0 auto;
  border-radius: 5px;
  background: $input-background;
  height: 50px;
 
}

.webflow-style-inputs{
     &:after {
    content: "";
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 999;
    height: 2px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-position: 0% 0%;
    background: linear-gradient(to right, #B294FF, #57E6E6, #FEFFB8, #57E6E6, #B294FF, #57E6E6);
    background-size: 500% auto;
    animation: gradient 3s linear infinite;
  }
}

.webflow-style-input input {
    padding:5px;
    border-radius: 0px;
    flex-grow: 1;
    color: $input-text-inactive;
    background: white;
    font-size: 1rem;
    line-height: 2rem;
    // border:1px solid #faf6f6;
    padding: 3px 3px 3px 10px;
    outline: 0px !important;
    border:0px !important;
    vertical-align: middle;
        box-shadow: 1px 1px 5px 0px #888888, -1px -1px 1px 0px #888888;
    //  box-shadow: inset 0 0 12px 12px white, inset 0 0 3px 2px white;
    // box-shadow: 2px 2px 2px #888888;
    &::-webkit-input-placeholder {
        color: $input-text-active;
    }
}


</style>