<template>

  <div class="select-box">
   
  <div class="options-container" style="margin-top:42px">
    <div class="option" v-for="item in options" :key="item">
      <input type="radio" class="radio" :value="item.mobile" :id="item.mobile"   />
      <label :for="item.mobile">{{item.mobile}}</label>
    </div>
   
  </div>
  
  <div class="selected">
   Mobile number
  </div>
 
  <div class="search-box">
    <input type="text" placeholder="Search.." />
  </div>

</div>
</template>

<script >
export default{



    props: {
        label: {
            type: String,
            default:'',
        },
        options: {
            type:Array(),
            default:[],
        },
       

        //  id: {
        //     type: String,
        //     default:'',
        // },
       
    },
    data(){
      return{
        valueData:''
      }
    },
    mounted(){

        let _this= this;
        const selected = document.querySelector(".selected");
        const optionsContainer = document.querySelector(".options-container");
        const searchBox = document.querySelector(".search-box input");
        const optionsList = document.querySelectorAll(".option");


        selected.addEventListener("click", () => {
        optionsContainer.classList.toggle("active");

        searchBox.value = "";
        filterList("");

        if (optionsContainer.classList.contains("active")) {
            searchBox.focus();
        }
        });

        optionsList.forEach(o => {
        o.addEventListener("click", () => {
          alert('ok')
            selected.innerHTML = o.querySelector("input").value;
            _this.valueData =  o.querySelector("input").value;
            // console.log(_this.valueData);
             _this.$emit('submit_event', _this.valueData);
            optionsContainer.classList.remove("active");
        });
        });

        searchBox.addEventListener("keyup", function(e) {
        filterList(e.target.value);
        console.log(e.target.value);
        });

        const filterList = searchTerm => {
        searchTerm = searchTerm.toLowerCase();
        optionsList.forEach(option => {
            let label = option.firstElementChild.nextElementSibling.innerText.toLowerCase();
            if (label.indexOf(searchTerm) != -1) {
            option.style.display = "block";
            } else {
            option.style.display = "none";
            }
        });
        };


            }
        }

</script>

<style lang="scss" scoped>

// .wrapper{
//   width: 100%;
//   height: 100vh;
  
// }

.select-box {
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 60px;
}
.select-box .options-container {
  background: white;
  height: 100px;
  width: 100%;
  opacity: 0;

  border-radius: 8px;
  overflow: hidden;

  order: 1;
  color: rgb(56, 52, 52);
 
}
.select-box .option, .selected {
  padding: 12px 24px;
  cursor: pointer;
}
.select-box .option .radio {
  display: none;
}
.selected {
  background: white;
  border-radius: 8px;
  margin-bottom: 8px;
  color: rgb(56, 52, 52);
  position: relative;
  order: 0;
  border: 1px solid #464749;
  box-shadow: 1px 1px 1px #ddd, -1px -1px 1px #ddd;

}
.selected::after{  
  position: absolute;
    content: "";
    width: 10px;
    height: 10px;
    top: 50%;
    right: 15px;
    transform: translateY(-50%) rotate(45deg);
    border-bottom: 2px solid rgb(38, 37, 37);
    border-right: 2px solid rgb(38, 37, 37);
    cursor: pointer;
    transition: border-color 0.4s;
  
}  
.select-box .option:hover {
  background: #F1FFF3;;
  border-radius: 5px;
  color: #8AD891;
 
}
.select-box .option input:hover{
    font-weight: 700px;
}

.search-box input {
  width: 100%;
  padding: 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  position: absolute;
  /* outline: none; */
  opacity: 0;
  outline: 1px solid black;
  outline-offset: -10px;
  z-index: 100;
  box-shadow: 1px 1px 1px #ddd, -1px -1px 1px #ddd;
 
  border: none;
}
/* .search-box input:focus {
  outline: none;
} */

.select-box .options-container.active {
  max-height: 240px;
  min-height: 240px;
  opacity: 1;
 overflow-y: scroll;
  transition: all 0.4s;
  box-shadow: 1px 1px 1px #ddd, -1px -1px 1px #ddd;
  
  /* margin-top: 54px; */
}
.select-box .options-container.active ~ .selected::after {
//   transform: rotateY(180deg);



    border: none;
    border-left: 2px solid rgb(38, 37, 37);
    border-top: 2px solid rgb(38, 37, 37);
  /* top: -6px; */
}
.select-box .options-container::-webkit-scrollbar {
  width: 7px;
  background: #888;
  border-radius:  8px ;
  height: 1px;
}
.select-box .options-container::-webkit-scrollbar-thumb {
  background: #464749;
  border-radius: 8px 8px 8px 8px;

}
.select-box .options-container.active ~ .search-box input {
  opacity: 1;
  pointer-events: auto;
}


</style>