<template>
    <div class="vue_select" :class="[divClass]">
        <label :class="labelClass">{{ label }}</label>
        <select 
            class="field" 
            :class="[classValue]"
            :value="modelValue"
            v-bind="$attrs"
            @select="$emit('update:selected', $event.target.selected)"
            @input="$emit('update:modelValue', $event.target.value)"
        >
        <option style="padding:10px" value="" selected disabled>{{ label }}</option>
            <option 
                :value="data.id" 
                v-for="(data, index) in options" 
                :key="index"
            >{{data.name.en}}</option>
        </select>
    </div>

</template>
<script>

export default {
    props: {
        label: {
            type: String,
            default:'',
        },
        options: {
            type:Array(),
            default:[''],
        },
        modelValue: {
            type:[String,Number],
            default:'',
        },
        placeholder: {
            type: String,
            default:'',
        },
        type: {
            type: String,
            default:'',
        },
        divClass: {
            type:[String,Number],
            default:'',
        },
        labelClass: {
            default:'',
        },
        classValue: {
            type:[String,Number],
            default:'',
        },
    },
    setup() {
        
    },
}
</script>
<style scoped>
    .vue_select {
        width: 100%;
        display: inline-block;
    }
    label {
        margin-bottom: 6px;
        display: inline-block;
        font-size: 14px;
    }
    .field {
        width: 100%;
        border: 1px solid #ddd;
        border-radius: 4px;
        padding: 0 12px;
        font-size: 13px;
        height: 38px;
    }
    option {
        padding: 4px 12px;
    }
    .field:focus {
        box-shadow: none;
        outline: none;
        /* border: 1px solid #5b8fce; */
    }
    .border_bottom {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid #ddd;
        color:#5b8fce;
    }
    .border_bottom:focus {
        border-bottom: 1px solid #5b8fce;
    }
    .border_none {
        border: none;
        color:#5b8fce;
    }
    .shadow {
        box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
    }
    .shadow:focus {
        box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
    }
    .bg_ddd {
        background-color: #f2f2f2;
    }
    .redius_0 {
        border-radius: 0;
    }
    .focus_0:focus {
        box-shadow: none;
        border: none;
    }

    .d-flex {
        display: flex;
        align-items: center;
    }
    .w_200 { width: 200px; }
    .w_400 { width: 400px; }



    .mb-2 {
        margin-bottom: 4px;
    }
    .mb-3 {
        margin-bottom: 8px;
    }
    .mb-4 {
        margin-bottom: 12px;
    }
</style>
