<template>
    <div class="vue_checkbox" :class="[divClass]">
        <label :class="labelClass" :for="id">{{label}}</label>
        <div class="checkbox" :class="[classValue]">
            <input type="checkbox" :value="label" :id="id">
            <span class="txt">{{checkboxTxt}}</span>
        </div>
    </div>
</template>

<script>
export default{
    props: {
        label: {
            type: String,
            default:'',
        },
      
        id: {
            type: String,
            default:'',
        },
        divClass: {
            type:[String,Number],
            default:'',
        },
        labelClass: {
            default:'',
        },
        classValue: {
            type:[String,Number],
            default:'',
        },
        checkboxTxt: {
            type:[String,Number],
            default:'',
        },
    },
}  
</script>

<style scoped>
.vue_checkbox {
    width: 100%;
    display: inline-block;
}
label {
    margin-bottom: 6px;
    display: inline-block;
    font-size: 14px;
}
.txt {
    display: block;
    font-size: 11px;
    color: #777;
}
.d-flex {
    display: flex;
}
.w_200 { width: 200px; }
.w_400 { width: 400px; }
.mb-2 {
    margin-bottom: 4px;
}
.mb-3 {
    margin-bottom: 8px;
}
.mb-4 {
    margin-bottom: 12px;
}
</style>